<template>

<div class="product__item">
    <div class="product__item__image">
        
        <div 
        v-if="product.sale_price > 0"
        class="product__item__sale_hint">
            <span>
                - {{ product.discount_perc }} %
            </span>
        </div>

        <div 
        v-if="product.ves != 'nan'"
        class="product__item__ves_hint">
            <span>
                 {{ product.ves }} 
            </span>
        </div>

        <a href="{% url 'products:product' product.id %}">
        <img :src="product.imgsrc" alt="">
        </a>
    </div>

    <div class="product__item__info">

        <div
         v-if="product.sale_price > 0"        
         class="product__item__price">
            <span class="product__item__price__sale"> 
                {{ product.sale_price }} Р
            </span>
            <span class="product__item__price__regular">
                {{ product.price }}
            </span>
            <span class="product__item__price__regular2"> 
                {{ product.price }} Р
            </span>
        </div>
        <a class="product__item__name"
        href="{% url 'products:product' product.id %}">
            {{ productsInCart }}
            {{ product.name }}
        </a>
        <div style="margin: 40 0;
        text-align: center;">
            <span class="product__item__addcart
            product__addtocart__global"
            :value="product.id">
                <span 
                v-if="this.isInCart(product.id)" 
                @click="deleteFromCart(product.id)"
                class="in__cart__buttonstyle noselect">
                Удалить с корзины
                </span>
                <span
                v-else
                @click="addToCart(product.id)"
                class="not__incart__buttonstyle noselect">
                    В корзину
                </span>

            </span>
        </div>
    </div>

</div>

</template>

<script>
import axios from 'axios'
export default {
  name: 'ProductCard',
  props: ['product'],
  data () {
      return {
      }
  },
  computed: {
      productsInCart() {
        return this.$store.getters.products_in_cart;
      }
  },
  methods: {
      isInCart(id) {
          var in_cart_ids = Object.keys(this.productsInCart)
          var includes = in_cart_ids.includes(id.toString())
          console.log(includes)
          if (includes) {
              return true;
          } else {
              return false;
          }
      },
      addToCart(product_id) {
       console.log('call add to cart method');   
       console.log('product id is', product_id);   
        axios({
            method: 'get',
            url: 'http://127.0.0.1:8000/cart/add_item_ajax?' + 
            'product_id='+product_id,
        }).then( function(response) {
            if (response.status == 200) {
                return null
            }
        })  
        this.$store.dispatch('addProductToCart', product_id);
        console.log('products in cart are', this.productsInCart)
      },
      deleteFromCart(product_id) {
       console.log('call delete from cart method');   
       console.log('product id is', product_id);   
        axios({
            method: 'get',
            url: 'http://127.0.0.1:8000/cart/delete_from_cart_ajax?' + 
            'product_id='+product_id,
        }).then( response => console.log(response))  
        this.$store.dispatch('deleteProductFromCart', product_id);
      }
  }
}
</script>


