import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import 'es6-promise/auto'

Vue.config.productionTip = true
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    products: [],
    products_in_cart: {},
  },
  mutations: {
    /* mutations will be here */
    setProducts(state, products_to_set) {
      products_to_set.forEach(function(product) {
        state.products.push(product)
      })
    },
    setProductsInCart(state, products_in_cart) {
      products_in_cart.forEach(function(product) {
        state.products_in_cart[product['product_id']] = product['quantity']
      })
    },
    addProductToCart(state, product_id) {
      state.products_in_cart[product_id] = 1
    },
    deleteProductFromCart(state, product_id) {
      delete state.products_in_cart[product_id.toString()]
    },
  },
  actions: {
    setProductsInCart(context, products_in_cart) {
      context.commit('setProductsInCart', products_in_cart)
    },
    setProducts(context, products) {
      context.commit('setProducts', products)
    },
    addProductToCart(context, product_id) {
      context.commit('addProductToCart', product_id)
    },
    deleteProductFromCart(context, product_id) {
      context.commit('deleteProductFromCart', product_id)
    },
  },
  getters: {
    products: state => {
      return state.products;
    },
    products_in_cart: state => {
      return state.products_in_cart;
    },
  }
})

new Vue({
  store,
  render: h => h(App),
}).$mount('#app')
