<template>
  <div id="app">
    <Products />
  </div>
</template>

<script>
import axios from 'axios'
import Products from './components/Products.vue';

export default {
  name: 'App',
  components: {
    Products,
  },
  mounted() {
    this.getProductsInCart();
    this.getProducts();
  },
  methods: {
      getProducts() {
          axios({
              method: 'get',
              url: 'http://127.0.0.1:8000/get_api/',
          }).then( response => this.$store.dispatch('setProducts', response.data.products))
      },
      getProductsInCart() {
        axios({
              method: 'get',
              url: 'http://127.0.0.1:8000/cart/get_products_in_cart_api',
          }).then( response => this.$store.dispatch('setProductsInCart', response.data['products_in_cart']))
      }
  }
}
</script>

